import React, { useCallback, useEffect, useState } from 'react';
import { AbitAdmissionConditionVals, IRealAbit, IRealJSON } from "../models/abit";
import { IAbit2GUlog, IConfig, IGUChecks, IGUSets, preLog, RSSn } from "../models/common";
import { SSPVOCodeStatuses } from "../models/sspvo";
import main, { genZeroAbit } from "../ts/main";
import { genUpdateFunc, loadFiles, spliceIntoChunks } from "../ts/utils";
import { MultiSelect, IMultiSelect } from "../components/MultiSel";
import { CheckBox, SimpleSelect } from "../components/SimpleSelect";
import useRun from "../hooks/run.hook";
import MakeBtn from "../components/MakeBtn";

const defaultConfig: IConfig = {
    limit: 1,
    onlyAch: false,
    changeStatus: false,
    searchInGUDB: false,
    addDocs: false,
    guStatuses: ["in_competition"],
    guChecks: ["agreement", "attestat"],
    guSets: ["agreement", "orig"],
    abitConditions: [
        "general_competition",
        "without_entry_tests",
        "by_unusual_quota",
        "by_special_quota",
        "by_target_quota"
    ]
};

const Itmo2GU: React.FC = () => {

    const [config, setConfig] = useState(defaultConfig);

    const {
        currAbit, sca,
        status, ss,
        counter, sc,
        saveAt, setSaveAt,
        resize, setResize,
        convert, setConvert,
        runBtn,
        start, setStart,
        end, setEnd
    } = useRun({
        convertDefault: true,
        disabledDefault: true,
        getZeroFunc: genZeroAbit,
        type: "abits",
        handlerFunc: main
    });

    const [fileName, setFileName] = useState("");

    const [bachelors, setBachelors] = useState<IRealAbit[]>([]);

    useEffect(() => {
        const len = bachelors.length;
        if (!len) {
            runBtn.remove();
            sc("");
            sca("");
            ss("");
        }
        setEnd(len);
    }, [bachelors, runBtn, sc, ss, sca, setEnd]);

    const loadFilesLocal = (event: React.ChangeEvent<HTMLInputElement>) => loadFiles(event, {
        setName: (s: string) => {
            setFileName(s);
        },
        post: () => {
            runBtn.enable();
            setStart(1);
            setEnd(0);
        },
        then: parseJSON,
        empty: () => setBachelors([])
    });

    const validate = (e: React.ChangeEvent<HTMLInputElement>, ss: RSSn) => {
        e.preventDefault();
        runBtn.enable();
        const max = bachelors.length;
        let newVal = e.target.value;
        console.log(newVal);
        const points = Number(newVal);
        const validPoints = (points < 0) ? 0 : (points > max) ? max : points;

        ss(validPoints);
    };

    const mergeJSONs = useCallback(async () => {
        sc("");
        runBtn.disable();
        runBtn.remove();

        const logs = genZeroAbit(config) as IAbit2GUlog;

        const genLog = genUpdateFunc(fileName + "AbitLog", convert, resize);

        const backs = bachelors.slice(start - 1, end);
        const total = backs.length;
        const splitedBacks = spliceIntoChunks(backs, resize);

        let c = resize;

        for (const abit of splitedBacks) {

            if (c > total) { c = total; }
            sc(`${Math.round(c / total * 10000) / 100}% (${c}/${total})`);

            const toPromise: Promise<preLog>[] = [];
            abit.forEach(uno => toPromise.push(main(uno, sca, ss, config)));
            await Promise.all(toPromise).then(localLogs => localLogs.forEach(([uid, log]) => logs[uid] = log));
            if (c % saveAt === 0) { runBtn.update(genLog(c, logs)); }
            c += resize;

        }

        runBtn.update(genLog(total, logs, false));
        runBtn.enable();

    }, [bachelors, runBtn, start, end, config, resize, saveAt, convert, fileName,
        sc, ss, sca]);

    const parseJSON = (str: string) => {
        const preresult: IRealJSON = JSON.parse(str);
        setBachelors(prev => [...preresult.result.bachelors, ...prev]);
    };

    const updateConfig = (e: React.ChangeEvent<HTMLInputElement>) => {
        const key = e.target.id as keyof IConfig;
        setConfig(prev => ({ ...prev, [key]: !config[key] }));
    };

    const msHandlerGUS = (e: IMultiSelect<SSPVOCodeStatuses>) => setConfig(prev => ({ ...prev, guStatuses: e.map(o => o.value) }));
    const msHandlerAC = (e: IMultiSelect<AbitAdmissionConditionVals>) => setConfig(prev => ({ ...prev, abitConditions: e.map(o => o.value) }));
    const msHandlerACh = (e: IMultiSelect<IGUChecks>) => setConfig(prev => ({ ...prev, guChecks: e.map(o => o.value) }));
    const msHandlerGUSet = (e: IMultiSelect<IGUSets>) => setConfig(prev => ({ ...prev, guSets: e.map(o => o.value) }));

    return (
        <div className="main">
            <input id="source" type="file" multiple accept="application/json" onChange={loadFilesLocal} />
            <label className="ssel">С <input
                type="number"
                id="start"
                value={start}
                onChange={e => validate(e, setStart)}
                className="numi"
            /> по <input
                    type="number"
                    id="end"
                    value={end}
                    onChange={e => validate(e, setEnd)}
                    className="numi"
                />
            </label>
            <CheckBox id="onlyAch" check={config.onlyAch} handler={updateConfig} text="Только ИД" />
            <CheckBox id="changeStatus" check={config.changeStatus} handler={updateConfig} text="Менять статус" />

            <label>Статус ГУ</label>
            <MultiSelect.GUStatuses handler={msHandlerGUS} defOpt={defaultConfig.guStatuses!} />

            <label>Условие ИТМО</label>
            <MultiSelect.AbitCondition handler={msHandlerAC} defOpt={defaultConfig.abitConditions!} />

            <label>Проверять</label>
            <MultiSelect.GUChecks handler={msHandlerACh} defOpt={defaultConfig.guChecks!} />

            <label>Проставлять</label>
            <MultiSelect.GUSets handler={msHandlerGUSet} defOpt={defaultConfig.guSets!} />

            <SimpleSelect.Save value={saveAt} setFunc={setSaveAt} />
            <SimpleSelect.Split value={resize} setFunc={setResize} />

            <CheckBox id="toCSV" check={convert} handler={e => setConvert(e.target.checked)} text="Конвертировать в CSV" />

            <MakeBtn p={runBtn.p} func={mergeJSONs} />
            <p>Абитуриент: {currAbit}</p>
            <p>Всего {counter}</p>
            <p>{status}</p>
        </div>
    );
};

export default Itmo2GU;