export function passportForm(data: Personally, uid: number, sspvo_id: number) {
    return {
        doc_series: data.documents.person_id.series,
        doc_number: data.documents.person_id.number,
        issue_date: formatIssuedDate(data.documents.person_id.issued_date),
        subdivision_code: data.documents.person_id.division_code,
        doc_organization: data.documents.person_id.division_name,
        surname: data.full_name.last_name,
        name: data.full_name.first_name,
        patronymic: data.full_name.middle_name,
        id_okcm: getOKSM(data.citizenship),
        id_document_type: getPassportId(data.documents.person_id.type),
        uid: `${uid}_person_id`,
        id_entrant: sspvo_id
    };
}

export function attestatForm(edu: Education, uid: number, sspvo_id: number, passport_id: number) {
    return {
        id_education_level: 2,
        id_entrant: sspvo_id,
        id_document_type: 7,
        id_ident_document: passport_id,
        doc_version: 31,
        doc_number: edu.document_education.number,
        issue_date: formatIssuedDate(edu.document_education.issued_date),
        doc_org: edu.university.name,
        uid: `${uid}_education`,
        doc_name: "Аттестат о среднем (полном) общем образовании",
    };
}

export function egeForm(edu: Education, exam: Exam, uid: number, sspvo_id: number, passport_id: number) {
    const id_subject = getSubjectId(exam.discipline);
    return {
        id_region: getRegionId(edu.university.region),
        id_subject: id_subject,
        mark: exam.scores,
        result_date: `${exam.year}-07-18`,
        id_entrant: sspvo_id,
        id_document_type: 3,
        id_ident_document: passport_id,
        issue_date: `${exam.year}-07-18`,
        doc_version: 47,
        doc_org: edu.university.name,
        uid: `${uid}_education_ege_${id_subject}`,
        doc_name: "Результат ЕГЭ"
    };
}

async function getGUPerson(fullAbit: IRealAbit, ss: RSS, config: IConfig, logObj: IMainLog) {

    const { sspvo_id, snils } = await getIdBySnilsOrFIO(logObj, logObj.snils);

    ss(`Снилс ${snils}`);
    console.log(`Снилс ${snils}`);
    const uid = fullAbit.application.user.student_details_id;
    ss(`ITMO UID ${uid}`);

    const egeLog: IegeLog[] = [];

    if (sspvo_id) {

        logObj.gu_id = sspvo_id;
        let is_ege = false;

        const short = await get<IShort>(`/api/entrants/${sspvo_id}/short`);
        logObj.gu_attestat_orig = false;

        if (short.done) {
            logObj.gu_attestat_orig = short.data.orig_doc;
        }

        ss(`Найден в базе ГУ ИТМО, id ${sspvo_id}`);

        let sspvo: ISSPVOObj = {
            passport: { exist: false, id: 0 },
            attestat: { exist: false, id: 0, count: 0 },
            ege: [],
            egeFull: {}
        };

        const checkDoc = (doc: Doc) => {
            const docType = doc.id_document_type;

            if (docType === 1 || docType === 40) {
                if (!logObj.passport_match || doc.checked) {
                    console.log("Найден Паспорт");
                    ss(`Найден Паспорт, id ${doc.id}`);
                    sspvo.passport.id = doc.id;
                    sspvo.passport.exist = true;
                    logObj.gu_series = doc.doc_series || "";
                    logObj.gu_number = doc.doc_number || "";
                    logObj.passport_match = (logObj.gu_series === logObj.abit_series && logObj.gu_number === logObj.abit_number);
                }
            }
            switch (docType) {
                case 503: //temp
                    if (doc.name_subject !== null) {
                        is_ege = true;
                        const exist = Object.hasOwn(sspvo.egeFull, doc.name_subject);
                        console.log(doc.name_subject, doc.mark, doc.id);
                        ss(`Найден ЕГЭ, ${doc.name_subject}, ${doc.mark}`);
                        egeLog.push({ id: doc.id, name: doc.name_subject, points: doc.mark || 0, status: true });
                        sspvo.ege.push(doc.name_subject);
                        if (!exist || (exist && doc.checked)) {
                            sspvo.egeFull[doc.name_subject] = { id: doc.id, points: doc.mark! };
                        }
                    }
                    break;
                case 7:
                case 9:
                case 10:
                    if (!logObj.attestat_match || doc.checked) {
                        console.log("Документ об образовании", doc.id);
                        ss(`Документ об образовании, ${doc.id}`);
                        sspvo.attestat.id = doc.id;
                        sspvo.attestat.count += 1;
                        sspvo.attestat.exist = true;
                        logObj.gu_attestat_number = doc.doc_number || "";
                        logObj.attestat_match = (logObj.abit_attestat_number === logObj.gu_attestat_number)
                            || (logObj.abit_attestat_number === doc.doc_series + logObj.gu_attestat_number);
                    }
                    break;
            }
        };

        const passportAns = await get<IDocsAns>(`/api/entrants/${sspvo_id}/docs/short?categories=identification`);
        if (passportAns.done) {
            if (passportAns.data.length === 0) {
                addLog(logObj, "error_passport", "Нет документов");

                if (config.addDocs) {
                    console.log("Добавление паспорта");
                    let formData = makeForm(passportForm(fullAbit.personally, uid, sspvo_id));
                    sspvo.passport.id = await postDocs("/api/docs/idents/add", formData);
                    sspvo.passport.exist = Boolean(sspvo.passport.id);
                }

            } else {
                passportAns.data[0]?.docs?.forEach(checkDoc);

                if (!logObj.passport_match && fullAbit.personally.documents.is_have_old_passport) {
                    logObj.error_passport = "Старый паспорт?";
                    const passport_old = fullAbit.personally.documents.passport_old;
                    logObj.abit_number = passport_old?.number;
                    logObj.abit_series = passport_old?.series;
                    sspvo.passport.exist = false;
                    passportAns.data[0]?.docs?.forEach(checkDoc);
                }
            }
        }

        const docsAns = await get<IDocsAns>(`/api/entrants/${sspvo_id}/docs/short?no_categories=identification`);
        if (docsAns.done) {

            if (docsAns.data.length === 0) {
                addLog(logObj, "attestat_error", "Нет Аттестата, нет ЕГЭ");
            } else {
                docsAns.data[0]?.docs?.forEach(checkDoc);
            }

            if (!sspvo.attestat.exist && config.addDocs) {
                console.log("Добавление аттестата");
                let formData = makeForm(attestatForm(fullAbit.education, uid, sspvo_id, sspvo.passport.id));
                sspvo.attestat.id = await postDocs("/api/docs/add", formData);
                sspvo.attestat.exist = Boolean(sspvo.attestat.id);
            }

            if (config.addDocs && (!is_ege || sspvo.ege.length < fullAbit.exams.exams.length)) {
                console.log("Что то с егэ");
                for (const exam of fullAbit.exams.exams) {
                    if (!sspvo.ege.includes(exam.discipline)) {
                        console.log("Добавление", exam.discipline);
                        let formData = makeForm(egeForm(fullAbit.education, exam, uid, sspvo_id!, sspvo.passport.id));
                        const ege_id = await postDocs("/api/docs/add", formData);
                        sspvo.egeFull[exam.discipline] = { id: ege_id, points: exam.scores };
                    }
                };
            }

            if (sspvo.attestat.exist) {

                if (!logObj.gu_attestat_orig) {
                    if (config.guSets?.includes("orig") || (logObj.passport_match && logObj.attestat_match)) {
                        await setOriginal(sspvo_id, sspvo.attestat.id, fullAbit, logObj);
                    }
                } else {
                    console.log("Оригинал!");
                }

            }

        }

        await checkAppls({ sspvo_id, uid, attestat_id: sspvo.attestat.id },
            fullAbit.achievements.achievements, ss, sspvo.egeFull, config, logObj, fullAbit);

    } else if (config.searchInGUDB) {

        console.log("Поиск в общей базе ГУ");

        if (fullAbit.personally.documents.person_id.scans.length !== 0) {

            const { name, surname, patronymic } = logObj;
            const gu_fio = { name, surname, patronymic };

            const ans = await post<ISearchAns>("/api/entrant/search", gu_fio);

            if (ans.done) {
                const id = ans.data.id;

                ss(`Найден в общей базе ГУ, id ${id}`);
                console.log(`Найден в общей базе ГУ, id ${id}`);

                const addAns = await post<ISearchAns>(`/api/entrants/${id}/link/add`, { ...gu_fio, id });

                if (addAns.done) {
                    ss("Добавлен в базу ИТМО");
                    await getGUPerson(fullAbit, ss, config, logObj);
                } else {
                    console.log(addAns.message);
                }

            }
        }

    } else {
        addLog(logObj, "error_gu_search", "Добавление новых людей отключено на ГУ!");
    }
}

