export const stopList2 = [
    "05.03.06",
    "12.03.01",
    "12.03.02",
    "12.03.05",
    "13.03.01",
    "13.03.02",
    "18.03.02"
];

const stopList: string[] = [];

async function checkAppl(appl: IApplication, aches: Achievement[],
    ids: IcheckApplIds, ss: RSS, fullEge: IEgeFull, config: IConfig, logObj: IMainLog) {

    const { id } = appl;
    const appl_code = appl.comment.slice(2, 10);
    ss(`Заявление ${appl_code}`);

    logObj.epgu_id = appl.uid_epgu || 0;
    logObj.appl_id = id;
    logObj.appl_name = appl_code + ".";

    const logAppl = { id, name: appl_code } as ILogAppls;
    logAppl.errors = [];
    logAppl.info = [];

    if (stopList.includes(appl_code.slice(0, 8))) {
        console.log("TEMP SKIP", appl_code);
        logAppl.errors.push("Вариативность ещё не починили на ГУ!");
    } else {

        const CHECK_STATUS = config.changeStatus;

        if (CHECK_STATUS) {

            const ans = await get<IVIAns>(`/api/applications/${id}/tests/list`);

            if (ans.done) {

                for (const vi of ans.data) {

                    if (vi.id_entrance_test_type === 4) {

                        if (!vi.app_entrance_test) {
                            // console.log(vi);
                            for (const ege of Object.keys(fullEge)) {

                                if (fullEge[ege].id === 0) {
                                    logAppl.errors.push("ID=0, " + vi.name_subject);
                                }

                                if (ege.includes(vi.name_subject)) {

                                    console.log("Добавление ЕГЭ в ВИ", vi.name_subject, fullEge[ege]);

                                    let ans = await post(`/api/applications/${id}/tests/add`, {

                                        id_entrance_test: vi.id,
                                        result_value: fullEge[ege].points,
                                        uid: null,
                                        id_document: fullEge[ege].id,
                                        document_name: "Результат ЕГЭ",
                                        id_result_source: 1

                                    });

                                    if (ans.done) {
                                        console.log("Добавлено", ege);
                                        logAppl.info.push(`Добавлено ЕГЭ ${ege}`);
                                    }

                                } else {
                                    // console.log("Нет результатов ЕГЭ", vi.name_subject);
                                    logAppl.errors.push("Нет результатов ЕГЭ " + vi.name_subject);
                                }

                            }
                        }
                    }
                }

                if (config.changeStatus) {

                    const ans = await setApplStatus(id, "in_competition", {
                        comment: "Публикация конкурсных списков ожидается...",
                        id_notices_types: 10
                    });
                    if (ans.done) {
                        console.log(ans.message, "В конкурсе!");
                    }
                }
            }
        }
        if (config.guChecks?.includes("sums")) {

            const ans = await get<ISums>(`/api/applications/${id}/points_sum`);
            if (ans.done) {

                let itmoAhesSum = aches.reduce((sum, ahc) => {
                    sum += ahc.scores;
                    return sum;
                }, 0);

                if (itmoAhesSum > 10) {
                    itmoAhesSum = 10;
                }

                if (ans.data.sum_achievements === itmoAhesSum) {
                    console.log("Суммы ИД совпадают!");
                    logAppl.sumStatus = "OK";
                } else if (ans.data.sum_achievements > itmoAhesSum) {
                    const status = `Сумма на ССПВО больше чем в ИТМО! ${ans.data.sum_achievements} ${itmoAhesSum}`;
                    console.log(status);
                    logAppl.sumStatus = status;
                } else if (ans.data.sum_achievements !== itmoAhesSum && config.addDocs) {
                    console.log("Суммы ИД не совпадают!", ans.data.sum_achievements, itmoAhesSum);

                    ss("Добавление ИД в систему...");

                    const ansAdd = await post<IDefaulAns<{
                        id_achievements: number;
                        id_application: number;
                    }>>(`/api/applications/${id}/achievements/add`, {
                        id_document: ids.attestat_id,
                        id_category: 11,
                        name: "Другие индивидуальные достижения",
                        mark: itmoAhesSum - ans.data.sum_achievements,
                        uid: `${ids.uid}_achievements_other_${id}`,
                        id_achievement: null
                    });

                    if (ansAdd.done) {
                        console.log(ansAdd.message);
                        logAppl.info.push("Добавлено ИД");
                    }

                }

                logAppl.sumTotal = ans.data.sum_tests + ans.data.sum_achievements;

                logObj.abit_sums_ach = itmoAhesSum;
                logObj.gu_sums_ach = ans.data.sum_achievements;
                logObj.gu_sums_ege = ans.data.sum_tests;
                logObj.gu_sums_total = logAppl.sumTotal;

            }
        }
    }
}

async function checkBen(id: number, logObj: IMainLog, abitCondition: AbitAdmissionConditionVals) {
    if (abitCondition !== "general_competition") {
        logObj.benefints = "Особое право не стоит";
        const ansApplMain = await get<MainAns>(`/api/applications/${id}/main`);
        if (ansApplMain.done) {
            const ben = ansApplMain.data.benefits;
            if (ben?.length !== 0) {
                if (ben?.[0]) {
                    logObj.benefints = ben[0].name_benefit;
                }
            }
        }
    }
}

async function checkAgree(id: number, logObj: IMainLog, dataOrig: DS | undefined, config: IConfig) {
    const agrAns = await get<IDefaulAns<{ agreed: boolean; }>>(`/api/applications/${id}/info/agreed`);

    if (agrAns.done) {
        logObj.agreement = Boolean(agrAns.data.agreed);

        if (!logObj.agreement && config.guSets?.includes("agreement")) {

            const ansInfo = await get<IAnsApplInfo>(`/api/applications/${id}/info`);
            if (ansInfo.done) {

                const prevInfo = ansInfo.data;
                prevInfo.agreed = true;
                prevInfo.agreed_date = dataOrig || "2022-07-28T15:00:00+03:00";
                const ansEdit = await post(`/api/applications/${id}/info/edit`, prevInfo);
                addLog(logObj, "agreement_log", `Согласие ${(ansEdit.done) ? "" : "не "}проставлено!`);
                logObj.agreement = ansEdit.done;
            }
        }
    }
}

interface IcheckApplIds {
    sspvo_id: number;
    uid: number | string;
    attestat_id: number;
}

function getAbitProg(logObj: IMainLog, selected_program: AgreementSelectedProgram): [string, AbitAdmissionConditionVals] {
    logObj.letter = "О";
    if (selected_program.financing === "contract") {
        logObj.letter = "К";
    } else {
        switch (selected_program.admission_condition) {
            case "by_special_quota":
                logObj.letter = "С";
                break;
            case "by_target_quota":
                logObj.letter = "Ц";
                break;
            case "by_unusual_quota":
                logObj.letter = "Л";
                break;
            case "on_state_line":
                logObj.letter = "И";
                break;
        }
    }
    return [selected_program.competitive_group?.title?.slice(0, 8) + ` (${logObj.letter})`, selected_program.admission_condition];
}

export async function checkAppls(ids: IcheckApplIds, aches: Achievement[], ss: RSS,
    fullEge: IEgeFull, config: IConfig, logObj: IMainLog, abit?: IRealAbit) {
    console.group("Проверка заявлений");

    const [abit_prog, abit_cond] = getAbitProg(logObj, abit!.application.agreement.selected_program);
    const ans = await get<IAppAns>(`/api/entrants/${ids.sspvo_id}/applications`);

    if (ans.done) {

        for (const appl of ans.data) {
            if (config.guStatuses!.includes(appl.code_status)) {
                if (appl.comment.includes(abit_prog)) {

                    console.group("Заявление", abit_prog);
                    await Promise.all([
                        checkBen(appl.id, logObj, abit_cond),
                        checkAgree(appl.id, logObj, abit?.application.user.is_send_original_updated_at, config),
                        checkAppl(appl, aches, ids, ss, fullEge, config, logObj),
                    ]);
                    console.groupEnd();

                }
            }
        }

    } else if (0) { ////////////////////////////////
        console.log("Нет заявлений!");

        if (abit) {

            for (const itmoAppl of abit.selected_programs) {

                let letter = "О";

                if (itmoAppl.financing === "contract") {
                    letter = "К";
                } else {
                    if (itmoAppl.admission_condition === "by_unusual_quota") {
                        letter = "Л";
                    }
                }

                const code_id = getGUApplId(`${itmoAppl.direction_code} (${letter})`);

                await post("/api/applications/add", {
                    "id_competitive_group": code_id,
                    "id_entrant": 90178,
                    "docs": [
                        {
                            "document_name": "Паспорт гражданина Российской Федерации",
                            "id": 75699,
                            "name_category": "idents",
                            "name_type": "Паспорт гражданина Российской Федерации",
                            "type": "idents"
                        },
                        {
                            "document_name": "Аттестат о среднем (полном) общем образовании",
                            "id": 113306,
                            "name_category": "docs",
                            "name_type": "Аттестат о среднем (полном) общем образовании",
                            "type": "docs"
                        }
                    ],
                    "ids_benefits": [],
                    "app_number": "1",
                    "registration_date": "2022-07-18T00:00:00+03:00",
                    "uid": null,
                    "rating": null,
                    "priority": 1,
                    "first_higher_education": true,
                    "agree_epgu": false,
                    "original_doc": null,
                    "agreed": null,
                    "agreed_date": null,
                    "need_hostel": false,
                    "special_conditions": false,
                    "id_target_organization": null
                });
            }
        }
    }

    console.groupEnd();
}

function checkKeys(logObj: IMainLog, config: IConfig) {
    if (!config.guChecks?.includes("sums")) {
        delete logObj.abit_sums_ach;
        delete logObj.gu_sums_ach;
        delete logObj.gu_sums_ege;
        delete logObj.gu_sums_total;
    }
}

