async function main(abit: IRealAbit, sca: RSS, ss: RSS, config: IConfig): Promise<preLog> {

    const logObj: IMainLog = {
        gu_id: -1,
        epgu_id: -1,
        phone: abit.personally.contact?.phone || "",
        full_name: "",
        delo: abit.application.number,
        snils: abit.personally.documents.snils?.number?.trim() || "",
        surname: abit.personally.full_name.last_name.trim(),
        name: abit.personally.full_name.first_name.trim(),
        patronymic: abit.personally.full_name.middle_name?.trim() || "",
        gu_series: "",
        gu_number: "",
        abit_series: abit.personally.documents.person_id.series?.trim() || "",
        abit_number: abit.personally.documents.person_id.number?.trim() || "",
        passport_match: false,
        abit_attestat_number: abit.education?.document_education?.number?.trim() || "",
        gu_attestat_number: "",
        attestat_match: false,
        gu_attestat_orig: false,
        appl_id: -1,
        appl_name: "",
        letter: "unknown",
        agreement: "unknown",
        abit_benefints: "",
        abit_financing: "",
        benefints: "",
        abit_sums_ach: -1,
        gu_sums_ach: -1,
        gu_sums_ege: -1,
        gu_sums_total: -1,
        agreement_log: "",
        admc_error: "",
        skip_admc: "",
        skip_achievements: "",
        error_gu_search: "",
        error_passport: "",
        attestat_error: "",
    };

    checkKeys(logObj, config);
    // setDefaultKeys(logObj);

    const currAbit = `${logObj.surname} ${logObj.name} ${logObj.patronymic}`;
    sca(currAbit);
    logObj.full_name = currAbit;

    const abitCondition = abit.application.agreement?.selected_program?.admission_condition;
    logObj.abit_benefints = abitCondition;
    logObj.abit_financing = abit.application.agreement?.selected_program?.financing;

    if (config.abitConditions?.includes(abitCondition)) {

        if ((config.onlyAch && abit.achievements?.is_have_achievements) || !config.onlyAch) {

            console.group(currAbit);
            console.log(abit.application.number);

            if (abit.application.agreement.selected_program) {
                await getGUPerson(abit, ss, config, logObj);
            } else {
                addLog(logObj, "admc_error", "Empty");
            }
            console.groupEnd();

        } else {
            addLog(logObj, "skip_achievements", "Нет ИД в Абите");
        }
    } else {
        // addLog(logObj, "skip_admc", "abit_condition");
    }
    return [abit.application?.user?.student_details_id || Date.now(), logObj];

}

export default main;